import React from "react"
import { Card, CardBody, Row, Col, Input, Button, } from "reactstrap"
import { AgGridReact } from "ag-grid-react"
import { ContextLayout } from "../../utility/context/Layout"
// import { ChevronDown } from "react-feather"
import axios from "axios";
import {connect} from "react-redux";
import Customize from "./CustomizeDeals"
import Chart from "react-apexcharts"

import Breadcrumbs from "../../components/@vuexy/breadCrumbs/BreadCrumb"

import "../../assets/scss/plugins/tables/_agGridStyleOverride.scss"
import "../../assets/scss/plugins/tables/_customAgGridStyleOverride.scss"
import "../../assets/scss/pages/app-email.scss"

class SalesPeople extends React.Component {
    state = {
        rowData: null,
        pinnedTopRowData: [],
        pinnedBottomRowData: [],
        paginationPageSize: 20,
        currenPageSize: "",
        getPageSize: "",
        defaultColDef: {},
        columnDefs: [
            {
                headerName: "Salesperson",
                field: "salesperson",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Sales",
                field: "sales",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Payable Gross",
                field: "payable_gross",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Front End",
                field: "front_end",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Back End",
                field: "back_end",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
            {
                headerName: "Total Gross",
                field: "total_gross",
                filter: true,
                width: 180,
                cellStyle: {color: 'black', 'font-size': "14px"}
            },
        ],
        composeMailStatus: false,
        options: {
            colors: this.props.themeColors,
            plotOptions: {
                bar: {
                    horizontal: true
                }
            },
            dataLabels: {
                enabled: false
            },
            xaxis: {
                categories: [
                    "Blake T",
                    "Bobby W",
                    "Damon M",
                    "Dominic T",
                    "Eli C",
                    "Iriving H",
                    "Jeff H",
                    "Kat C",
                    "Patrick K",
                    "Scott L",
                    "Torri F",
                    "Tre G",
                ],
                tickAmount: 5
            }
        },
        series: [
            {
              data: [8.5, 2.5, 2.5, 2.5, 2.0, 2.5, 3.0, 2.0, 1.0, 13.5, 2.0, 4.0]
            }
        ]
    }

    componentDidMount() {
        axios.get("/api/aggrid/data").then(response => {
            // let rowData = response.data.data // replace for product
            let tempdata = {
                salesperson: "Jake J",
                sales: "14",
                payable_gross: "$1,582.00",
                front_end: "$3,532.00",
                back_end: "$16,500.00",
                total_gross: "$42,031.00"
            };

            let colWidth = window.innerWidth,
            columnDefs = this.state.columnDefs
            for (let i = 0; i < this.state.columnDefs.length; i++) {
                columnDefs[i].width = (colWidth-(colWidth/5)) / this.state.columnDefs.length;
                columnDefs[i].minWidth = 120;
            }

            let rowData = [];
            for (let i = 0; i < 20; i++) {
                rowData.push(Object.assign({}, tempdata));
            }

            JSON.stringify(rowData)
            this.setState({
                rowData,
                columnDefs
            });
        })
    }

    UNSAFE_componentWillMount () {
        this.initTable(this.props);
    }

    UNSAFE_componentWillReceiveProps (nextprops) {
        this.initTable(nextprops);
    }

    initTable(props) {
        let pinnedBottomRowData = [];
        pinnedBottomRowData.push(
            {
                salesperson: "Total",
                sales: "46",
                payable_gross: "$1,582.00",
                front_end: "$3,532.00",
                back_end: "$16,500.00",
                total_gross: "$42,031.00"
            }
        );
        pinnedBottomRowData.push(
            {
                salesperson: "Average",
                payable_gross: "$1,582.00",
                front_end: "$3,532.00",
                back_end: "$16,500.00",
                total_gross: "$42,031.00"
            }
        );

        this.setState({
            pinnedBottomRowData,
            composeMailStatus: false
        });
    }

  onGridReady = params => {
    this.gridApi = params.api
    this.gridColumnApi = params.columnApi
    this.setState({
      currenPageSize: this.gridApi.paginationGetCurrentPage() + 1,
      getPageSize: this.gridApi.paginationGetPageSize(),
      totalPages: this.gridApi.paginationGetTotalPages()
    })
  }

  updateSearchQuery = val => {
    this.gridApi.setQuickFilter(val)
  }

  filterSize = val => {
    if (this.gridApi) {
      this.gridApi.paginationSetPageSize(Number(val))
      this.setState({
        currenPageSize: val,
        getPageSize: val
      })
    }
  }
  handleCustomize() {
    this.handleComposeSidebar("open")
    this.onSetSidebarOpen(false)
  }
  handleComposeSidebar = status => {
    if (status === "open") {
      this.setState({
        composeMailStatus: true
      })
    } else {
      this.setState({
        composeMailStatus: false
      })
    }
  }

  onSetSidebarOpen = open => {
    this.setState({ sidebarOpen: open })
  }
  handleMainAndComposeSidebar = () => {
    this.handleComposeSidebar("close")
    this.onSetSidebarOpen(false)
  }

  render() {
    const { rowData, defaultColDef, pinnedBottomRowData } = this.state

    return (
    <React.Fragment>
        <Breadcrumbs breadCrumbTitle="Sales People" breadCrumbParent="Reports" breadCrumbActive="Sales People"/>
        <Card onClick={this.handleMainAndComposeSidebar}>
            <CardBody>
                <div className="d-flex flex-wrap justify-content-end align-items-center">
                    <div className="mb-1"></div>
                    <div className="d-flex flex-wrap justify-content-between mb-1">
                        <div className="table-input mr-1">
                            <Input placeholder="search..." onChange={e => this.updateSearchQuery(e.target.value)} value={this.state.value} />
                        </div>
                        <div className="customize-btn">
                            <Button.Ripple block color="primary" onClick={() => this.handleCustomize()} >
                                Customize
                            </Button.Ripple>
                        </div>
                        <div className="export-btn">
                            <Button.Ripple color="primary" onClick={() => this.gridApi.exportDataAsCsv()} >
                                Export as CSV
                            </Button.Ripple>
                        </div>
                    </div>
                </div>
                <div style={{padding: "10px 20px"}}>
                    <Row>
                        <Col xs="3">
                            <Row>
                                <Col xs="6">Deals:</Col>
                                <Col xs="6">46</Col>
                            </Row>
                        </Col>
                        <Col xs="3">
                            <Row>
                                <Col xs="6">Company Total:</Col>
                                <Col xs="6">$108,754.00</Col>
                            </Row>
                        </Col>
                        <Col xs="3">
                            <Row>
                                <Col xs="6">Front End Total:</Col>
                                <Col xs="6">$44,272.00</Col>
                            </Row>
                        </Col>
                        <Col xs="3">
                            <Row>
                                <Col xs="6">Back End Total:</Col>
                                <Col xs="6">$64,482.00</Col>
                            </Row>
                        </Col>
                    </Row>
                </div>
                <Chart options={this.state.options} series={this.state.series} type="bar" height={350} />
                <div className="d-flex flex-wrap mb-1">
                    <div className="mr-1">
                        <Button.Ripple block color="primary">
                            Total Sales
                        </Button.Ripple>
                    </div>
                    <div className="mr-1">
                        <Button.Ripple block color="primary">
                            Total Gross
                        </Button.Ripple>
                    </div>
                    <div className="mr-1">
                        <Button.Ripple block color="primary">
                            Average Gross
                        </Button.Ripple>
                    </div>
                </div>
            </CardBody>
        </Card>
        <div className="email-application">
            <Customize handleComposeSidebar={this.handleComposeSidebar} currentStatus={this.state.composeMailStatus}/>
            <Card className="agGrid-card" style={{height: "100% !important", paddingBottom: "2%"}}>
                <CardBody className="py-0" style={{height: "100% !important"}}>
                {
                this.state.rowData === null ? null : (
                    <div className="ag-theme-material w-100 my-2 ag-grid-table" style={{height: "100% !important", width: "100% !important"}}>
                        <ContextLayout.Consumer>
                            {context => (
                            <AgGridReact
                                gridOptions={{}}
                                rowSelection="multiple"
                                defaultColDef={defaultColDef}
                                columnDefs={this.state.columnDefs}
                                rowData={rowData}
                                onGridReady={this.onGridReady}
                                floatingFilter={true}
                                pagination={true}
                                enableRtl={context.state.direction === "rtl"}
                                
                                pinnedBottomRowData={pinnedBottomRowData}
                                rowHeight="40"
                            />
                            )}
                        </ContextLayout.Consumer>
                    </div>
                )}
                </CardBody>
            </Card>
        </div>
    </React.Fragment>
    )
  }
}

const mapStateToProps = state => {
    return {
        app: state.calender
    }
}

export default connect(mapStateToProps, {})(SalesPeople);
